<template>
  <div class="form">
    <header class="header">
      <strong>账号绑定</strong>
    </header>
    <ul class="field-group">
      <li class="field-item" ref="uid">
        <label for="uid">职工编号:</label>
        <input
          type="text"
          id="uid"
          v-model="user.uid"
          placeholder="请填写职工编号"
          @change="change"
          @blur="change"
          :disabled="disabledForm"
        />
      </li>
      <li class="field-item" ref="username">
        <label for="username">用户名:</label>
        <input
          type="text"
          id="username"
          v-model="user.username"
          placeholder="请填写用户名"
          @change="change"
          @blur="change"
          :disabled="disabledForm"
        />
      </li>
      <li class="field-item" ref="idCard">
        <label for="idCard">身份证号:</label>
        <input
          type="text"
          id="idCard"
          v-model="user.idCard"
          placeholder="请填写身份证号"
          @change="change"
          @blur="change"
          :disabled="disabledForm"
        />
      </li>
      <li class="field-item" ref="phone">
        <label for="phone">联系电话:</label>
        <input
          type="phone"
          id="phone"
          v-model="user.phone"
          placeholder="请填写联系电话"
          @change="change"
          @blur="change"
          :disabled="disabledForm"
        />
      </li>
    </ul>
    <div class="btn-box">
      <input v-if="disabledForm" class="cancel" type="cancel" value="取消绑定" @click="cancel" />
      <input v-else class="submit" type="submit" value="绑定身份" @click="submit" />
    </div>
    <Toast :show="showToast" :isSuccess="isSuccess" :msg="msg" @close="closeToast" />
  </div>
</template>
<script>
import axios from 'axios'
import { apiHost } from '@/utils/config'
import { validate } from '@/utils/validate'
import Toast from '@/components/Toast'
export default {
  components: {
    Toast
  },
  data () {
    return {
      showToast: false,
      msg: '',
      isSuccess: false,
      user: {
        username: '',
        uid: '',
        idCard: '',
        phone: ''
      },
      disabledForm: false
    }
  },
  mounted () {
    this.getUser('admin')
  },
  methods: {
    closeToast () {
      this.showToast = false
    },
    submit () {
      if (this.validate()) {
        console.log('submit')
      }
    },
    cancel () {
      console.log('cancel')
    },
    change (field) {
      const { id, value } = field.target
      const v = validate(id, value)
      let p
      if (v) {
        this.$refs[id].classList.add('error')
        if (!this.$refs[id].querySelector('.err-msg')) {
          p = document.createElement('p')
          p.className = 'err-msg'
          this.$refs[id].appendChild(p)
        }
        this.$refs[id].querySelector('.err-msg').innerText = v
      } else {
        this.$refs[id].classList.remove('error')
        this.$refs[id].querySelector('.err-msg') &&
          this.$refs[id].removeChild(this.$refs[id].querySelector('.err-msg'))
      }
    },
    validate () {
      const fields = ['uid', 'username', 'idCard', 'phone']
      fields.map(item => {
        const field = {
          target: {
            id: item,
            value: this.user[item]
          }
        }
        this.change(field)
      })
      return document.querySelectorAll('.field-group .err-msg').length === 0
    },
    getUser (uid) {
      axios
        .get(`${apiHost}/dinner/a/wechat/api/user/getUser`, {
          params: {
            loginName: uid
          }
        })
        .then(res => {
          if (res.data.body.user) {
            const { id, loginName, name, mobile, idcard, openId } = res.data.body.user
            this.user = {
              id,
              openId,
              uid: loginName,
              username: name,
              phone: mobile,
              idCard: idcard
            }
            if (openId && loginName) {
              this.disabledForm = true
            }
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    bindUser (data) {
      const { uid, username, phone, idCard } = data
      const openId = localStorage.getItem('openId')
      axios
        .post(`${apiHost}/dinner/a/wechat/api/user/binding`, {
          openId,
          loginName: uid,
          name: username,
          mobile: phone,
          idcard: idCard
        })
        .then(res => {
          this.bindUser = {
            status: res.success,
            msg: res.msg
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    unBindUser () {
      const { uid, openId } = this.user
      axios
        .post(`${apiHost}/dinner/a/wechat/api/user/unbinding`, {
          openId,
          loginName: uid
        })
        .then(res => {
          this.bindUser = {
            status: res.success,
            msg: res.msg
          }
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>
